.masonry{
  width: 100%;
}
.profile{
  width: 100%;
  
}
.profile-header{
  height: 244px;
  margin: 2rem auto;
  padding: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
}

.user-info{
  width: 50%;
  margin: 0 1.5rem;
}
.user-info h1{
  font-size: 2.5rem;
}
.user-info p,
.user-stats p{
  color: rgba(38,50,56,.54);
}
.user-stats{
  margin-top: 1rem;
  width: 20%;
}
.user-stats p{
  margin-bottom: 1.5rem;
}
.user-stats p span{
  display: block;
  font-size: 1.25rem;
  color: #000;
}
.user-gravatar{
  border-radius: 50%;
  max-width: 100px !important;
  max-height: 100px !important;
}
@media screen and (min-width: 800px){
  .user-gravatar{
    max-width: 100% !important;
    max-height: none !important;
  }
}


